// ** React Imports
import { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** UserCars Edit Components
import AccountTab from './Account'

// ** Store & Actions
import { getAutoServiceCategory } from '@store/AutoServiceCategory/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Third Party Components
import { Alert, Card, CardBody, Col, Row } from 'reactstrap'

// ** Custom Components
import UILoader from '@components/ui-loader'

// ** Styles
import '@styles/react/apps/app-users.scss'

const AutoServiceCategoryEdit = () => {
  // ** States & Vars
  const [activeTab, setActiveTab] = useState('1'),
    { isLoading, selectedData } = useSelector(state => state.autoServiceCategories),
    dispatch = useDispatch(),
    { id } = useParams()

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

  // ** Function to get AutoServiceCategory on mount
  useEffect(() => {
    dispatch(getAutoServiceCategory(id))
  }, [dispatch])

  return selectedData !== null && selectedData !== undefined ? (
    <UILoader blocking={isLoading}>
      <Row className='app-user-edit'>
        <Col sm='12'>
          <Card>
            <CardBody className='pt-2'>
              <AccountTab selectedData={selectedData} />
            </CardBody>
          </Card>
        </Col>
      </Row>
    </UILoader>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>AutoServiceCategory not found</h4>
      <div className='alert-body'>
        AutoServiceCategory with id: {id} doesn't exist. Check list of all autoServiceCategories: <Link to='/AutoServiceCategory/list'>AutoServiceCategories List</Link>
      </div>
    </Alert>
  )
}

export default AutoServiceCategoryEdit
